import styled from '@emotion/styled';

export const CurrentContainer = styled.div`
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  @media screen and (min-width: 320px) {
    max-width: 480px;
  }
  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 75px;
    padding-right: 75px;
  }
  @media screen and (min-width: 1024px) {
    max-width: 1024px;
    padding-left: 70px;
    padding-right: 70px;
  }
`